@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff"); }

.wrapper {
  box-sizing: border-box;
  max-width: 1280px;
  margin: 0 auto; }

.visually-hidden {
  position: absolute;
  transform: scale(0); }

.button {
  box-sizing: border-box;
  background: none;
  border: none;
  cursor: pointer; }

.header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%); }

.header__container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 30px 90px 35px 30px; }

.header__contacts {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 27px;
  padding-top: 7px;
  margin-right: -7px; }
  @media (max-width: 1023px) {
    .header__contacts span {
      position: absolute;
      transform: scale(0); } }

@media (max-width: 1023px) {
  .header__logo-words {
    position: absolute;
    transform: scale(0); } }

@media (max-width: 767px) {
  .header__logo img {
    width: 35px;
    height: 35px; } }

.header p {
  color: #ffffff; }

.header a {
  text-decoration: none;
  border-radius: 3px; }
  .header a:hover {
    box-shadow: 0 0 0 4px #ffffff; }
  .header a:active {
    box-shadow: 0 0 0 1px #ffffff;
    opacity: 0.3; }

.header__callback-button {
  margin-top: 5px; }
  @media (max-width: 1023px) {
    .header__callback-button {
      display: none; } }

.logo {
  display: grid;
  grid-template-columns: min-content max-content; }

.logo span {
  display: block;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.7);
  margin-top: 1px; }

.logo p {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  line-height: 29px;
  font-weight: 700;
  color: #f2f5f6;
  margin: 0; }

.logo__words {
  padding-top: 5px;
  margin-left: 15px; }

.contact {
  display: grid;
  grid-template-columns: min-content 1fr;
  gap: 15px;
  padding: 8px; }
  .contact::before {
    content: "";
    display: block;
    width: 21px;
    height: 20px;
    background: #00b2ff; }
    @media (max-width: 1023px) {
      .contact::before {
        width: 20px;
        height: 20px; } }
  .contact--email::before {
    mask: no-repeat center/cover url("../img/svg/contacts-sprite.svg#email"); }
  .contact--phone::before {
    mask: no-repeat center/cover url("../img/svg/contacts-sprite.svg#phone"); }

.contact span {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #f2f5f7;
  text-decoration: none; }

.callback-button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  color: #f2f5f6;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  min-height: 40px;
  max-width: 160px;
  width: 100%;
  padding: 7px 15px 9px;
  border-radius: 3px;
  border: solid 1px #f2f5f6; }

.footer {
  background: #041827; }

.footer__wrapper {
  display: flex; }

.social__list {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0; }

.navigation h2 {
  color: #ffffff;
  margin: 0; }

.navigation__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-flow: dense;
  list-style: none;
  margin: 0;
  padding: 0; }

